// 
// right-sidebar.scss
//

$timeline-icon-width: 20px;

.right-bar {
    background-color: $white;
    box-shadow: 0 0 24px 0 rgba($dark, 0.1), 0 1px 0 0 rgba($dark, 0.08);
    display: block;
    position: fixed;
    transition: all 200ms ease-out;
    width: $rightbar-width;
    z-index: 9999;
    float: right !important;
    right: -($rightbar-width + 10px);
    top: 0;
    bottom: 0;
    
    .rightbar-content {
        padding-bottom: 70px;
    }

    .rightbar-title {
        background-color: $dark;
        padding: 27px 25px;
        color: $white;
    }
    .right-bar-toggle {
        background-color: lighten($dark, 7%);
        height: 24px;
        width: 24px;
        line-height: 24px;
        color: $white;
        text-align: center;
        border-radius: 50%;
        margin-top: -4px;
        &:hover {
            background-color: $primary;
        }
    }
}

.right-bar-enabled {
    .right-bar {
        right: 0;
    }
}

// Timeline
.timeline-alt {
    padding: 20px 0;
    position: relative;
    &:before {
        background-color: $light;
        bottom: 30px;
        content: "";
        left: 9px;
        position: absolute;
        top: 30px;
        width: 2px;
        z-index: 0;
    }
    .timeline-item {
        position: relative;
        opacity: 0.6;
        &.completed {
            opacity: 1;
        }
        &.current { 
            opacity: 1;
            .timeline-icon {
                &:before {
                    content: '';
                    position: absolute;
                    top: -$timeline-icon-width/2;
                    left: -$timeline-icon-width/2;
                    padding: $timeline-icon-width;
                    background-color: currentColor;
                    border-radius: 50%;
                    opacity: 0;
                    z-index: -1;
                    animation-name: animation-timeline-icon-current;
                    animation-duration: 2s;
                    animation-iteration-count: infinite;
                    animation-timing-function: ease-out;
                }
            }
        }
        .timeline-icon {
            float: left;
            height: $timeline-icon-width;
            width: $timeline-icon-width;
            border-radius: 50%;
            border: 2px solid transparent;
            font-size: 12px;
            text-align: center;
            line-height: 16px;
            background-color: $white;
        }
        .timeline-item-info {
            margin-left: 30px;
        }
    }
}

@include media-breakpoint-down(sm) {
    .right-bar {
        overflow: auto;
        .slimscroll-menu {
            height: auto !important;
        }
    }
}

@keyframes animation-timeline-icon-current {
    from {
        transform: scale(0);
        opacity: 1;
    }
    to {
        transform: scale(1);
        opacity: 0;
    }
}